.wallpaper {
    position: absolute;

    top: 0vh;
    left: 0vh;
    height: 100%;
    width: 100%;
    background-position: -10vh;
    background-size: contain;

}

.wrapper {
    position: absolute;
    overflow: hidden;
    left: 0vh;
    top: 0vh;
    width: 100%;
    height: 100%;
}
* {
    user-select: none;
}

.inventory_wrapper {
    position: absolute;
    height: fit-content;
    width: fit-content;
    top: 50%;
 
    left: 50%;
    transform: translate(-50%, -50%);

    display: flex;
}

.inventory {
    position: relative;
    padding: 1vh;
    height: fit-content;
    background-color: rgba(29, 29, 29, 0.9);
    border-radius: .3vh;
    margin-right: .5vh;
    box-shadow: rgba(0, 0, 0, 0.5) 0 0 5px;
}

.inventory_header {
    position: relative;
    height: 7vh;
    background-color: #202020;
   
    border-radius: .3vh;
    margin-bottom: 1vh;
    
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.inventory_header_name {
    position: relative;
    margin-left: 1vh;
    font-family: "avenir";
    color: rgba(255, 255, 255, 0.5);
    font-size: 1vh;
    text-transform: uppercase;
    letter-spacing: .2vh;
    font-weight: bold;
}

.inventory_items {
    position: relative;
    display: flex;
    max-height: 45vh;
    overflow: auto;
    width: fit-content;
    padding-right: .5vh;

    border-radius: .3vh;
}

article {
    position: relative;
    margin-right: .2vh;
    width: fit-content;
}

article:last-child {
    margin-right: 0vh;
}

article:last-child > .inventory_slot {
    border-right: unset !important;
}

.inventory_slot {
    position: relative;
    width: 6vh;
    height: 6vh;
    box-shadow: rgba(0, 0, 0, 0.2) 0 0 5px inset;
    border-radius: .2vh;

    margin-bottom: .2vh;


    background-color: #202020;
    transition: background 0.1s;
}

.inventory_slot_taken {
    background-color: transparent;
}
.inventory_slot_taken .inventory_slot_index {
    display: none;
}

.inventory_slot_index {
    position: absolute;
    top: 1vh;
    left: 1vh;
    color: rgba(255, 255, 255, 0.4);
    font-family: "avenir";
    font-size: 1vh;
}

.inventory_slot_inner {
    position: absolute;
    border-radius: .2vh;
    z-index: 5;

    box-sizing: border-box;
    background-color: #202020;
    box-shadow: rgba(0, 0, 0, 0.2) 0 0 5px inset;
    overflow: hidden;
    transition: background 0.2s;
}

.inventory_slot_inner_rotated > .inventory_slot_ammo {
    position: absolute;
    left: .5vh;
    right: unset !important;
    writing-mode: vertical-rl;
}
.inventory_slot_ammo {
    position: absolute;
    font-family: "avenir";
    color: rgba(255, 255, 255, 0.4);

    font-size: 0.8vh;

    right: .5vh;
    font-family: "avenir";
    bottom: .5vh;
}

.inventory_slot_inner:hover {
    background-color: #494848;
}

.inventory_slot_inner_rotated {

}

.inventory_slot_inner_rotated > .inventory_slot_label {
    position: absolute;
    left: .1vh;
    top: .1vh;
    bottom: unset;
    writing-mode: vertical-rl;
}

.inventory_slot_inner_rotated > .inventory_slot_weight {
    position: absolute;
    right: .1vh;
    top: .1vh;
    bottom: unset;
    writing-mode: vertical-rl;
}


.inventory_slot_weight {
    position: absolute;

    top: .1vh;
    left: .1vh;

    border-radius: .5vh;
    padding: .5vh;
    z-index: 5;
   
    font-family: "avenir";
    color: rgba(255, 255, 255, 0.4);
    font-size: 0.8vh;
    text-transform: uppercase;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-rendering: optimizeLegibility;
}

.inventory_slot_inner_rotated > .inventory_slot_amount {
    position: absolute;

    right: unset;
    top: unset;

    bottom: .1vh;
    right: .1vh;
    writing-mode: vertical-rl;
}

.inventory_slot_amount {
    position: absolute;

    top: .1vh;
    right: .1vh;

    border-radius: .5vh;
    padding: .5vh;
    z-index: 5;
   
    font-family: "avenir";
    color: rgba(255, 255, 255, 0.4);
    font-size: 0.8vh;
    text-transform: uppercase;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-rendering: optimizeLegibility;
}

.inventory_slot_label {
    position: absolute;
    max-width: 100%;
    word-break:normal;
    bottom: .1vh;
    left: .1vh;

    border-radius: .5vh;
    padding: .5vh;
    z-index: 5;
   
    font-family: "avenir";
    color: rgba(255, 255, 255, 0.4);
    font-size: 0.8vh;
    text-transform: uppercase;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-rendering: optimizeLegibility;
}

.inventory_slot:hover {
    background-color: #49484880
}

::-webkit-scrollbar {
    width: .3vh;
    background-color: rgba(255, 255, 255, 0.4);
}

::-webkit-scrollbar-thumb {
    background-color: #c9c9c9;
}

.inventory_header_weight {
    position: relative;
    height: .5vh;
    border-radius: .2vh;
    margin: 2vh 1vh 0 1vh;
 
    background-color: #fcf7f738;
}

.inventory_header_weight_inner {
    position: absolute;
    left: 0vh;
    background: linear-gradient(to right, #c9c9c9 90%, #c9c9c900);
    
    height: 100%;
    border-radius: .2vh;
    width: 60%;
}

.inventory_header_weight_number {
    position: absolute;
    right: 1vh;
    top: 0vh;
    font-weight: 100;
    font-family: "avenir";
    color: rgba(255, 255, 255, 0.4);
    font-size: 1vh;
    text-transform: uppercase;
    letter-spacing: .2vh;
}

.inventory_slot_inner_rotated > img {
    height: 80%;
    width: unset !important;
}

.inventory_slot_inner > img {
    position: absolute;
    left: 50%;
    top: 50%;
    filter: drop-shadow(rgb(0, 0, 0) 0 0 2px);
    width: 80%;
    transform: translate(-50%, -50%);
}

