.inventory_use {
    position: absolute;
    right: 0vh;
    width: 2.5vh;
    background-color: #57555cfb;
    border-radius: .3vh;

    height: 2.5vh;
    transform: translate(0, -50%);
    top: 50%;
}

.inventory_use input {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    height: 50%;
    width: 50%;
    
    text-align: center;
    border: none;
    background-color: transparent;
    outline: none;
    color: white;
    font-family: "avenir";
    font-size: 1.1vh;
    text-shadow: black 0 0 5px;
}

.inventory_use input::-webkit-inner-spin-button {
    display: none;
}

.inventory_slot_inner_rotated .inventory_slot_durability {
    position: absolute;
    left: .5vh;
    bottom: .5vh;
    height: 3vh;

    overflow: hidden;
    width: .2vh;
} 

.inventory_slot_durability {
    position: absolute;
    bottom: .5vh;
    right: .5vh;

    background-color: rgba(255, 255, 255, 0.5);
    width: 3vh;
   
    overflow: hidden;
  
    height: .3vh;
}

