} */

.inventory_slot_durability_inner {
    position: absolute;
    height: 100%;
    width: 100%;
  
    background-color: #912a40e5;
}

.rare_item img {
    filter: drop-shadow(rgba(147, 71, 177, 0.4) 0 0 10px) !important;
}
.legendary_item img {
    filter: drop-shadow(rgba(212, 194, 25, 0.2) 0 0 10px) !important;
}
.good_item img {
    filter: drop-shadow(rgba(72, 209, 38, 0.2) 0 0 10px) !important;
}
.poor_item img {
    filter: drop-shadow(rgba(78, 89, 190, 0.3) 0 0 10px) !important;
}